::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  // width: 2px;
  height: 2px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: $orange-900;
  border-radius: 5px
}

.container {
  @include container(15);
}

.sr-only {
  @include sr-only();
}

body {
  @include safe-font("Rubik");
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: $dark;
  background-color: $silver-100;
  @include font-media(20, 16);
}

.logo {
  display: inline-block;
  border-radius: 5px;
  @include logo-actions();

  &__img {
    width: 200px;
    height: 39px;
  }
}

.link {
  border-radius: 5px;
  @include link-actions();

  &--active {
    color: $orange-900;
  }
}

.title {
  font-weight: 500;
  line-height: 1.3;
  font-size: 36px;
  text-align: center;
  color: $darkBlue-900;
  @include font-media(36, 24);

  &--span {
    display: block;
    text-align: left;
  }
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  border-radius: 5px;
  background-color: $orange-900;
  @include btn-bg-actions();
}

.popular,
.restaurant,
.apps {
  padding: 120px 0;

  @include r(962) {
    padding: 90px 0;
  }

  @include r(787) {
    padding: 60px 0;
  }

  @include r(560) {
    padding: 40px 0;
  }
}

.lock {
  overflow: hidden;
  position: relative;

  .hero__link {
    z-index: -1;
  }
}



